            <img :src="circles" alt="" class="circles absolute">
            <VueSlickCarousel v-bind="settings" class="carousel">
                <div v-for="(quote, index) in quotes" :key="index">
                    <h3 v-html="quote.phrase" class="serif relative"></h3>
                </div>
            </VueSlickCarousel>
        </section>
    </section>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
    name:'Quotes',
    data(){
        return{
            quotes: [
                {
                    phrase:`I'm never satisfied with only ideas, I have the urge to act on them.`
                },
                {
                    phrase:`I believe in punctuality, honesty, and quality of work.`
                },
                {
                    phrase:`I'm passionate about the idea of working in multicultural environments.`
                },
                {
                    phrase:`Coding is like a bridge. It passes over an initial problem and brings users to a solution.`
                }
            ],
            settings:{
                "dots": true,
                "infinite": true,
            },
            circles:require('../../assets/resources/circles.svg'),
        }
    },
    components:{ VueSlickCarousel }
}
