<style lang="scss" >
@import "../../variables.scss";

.whole-quotes-container{
    background:$white;
    overflow-x: hidden;

    .quotes-container{
        padding:2rem 2rem;
        text-align: center;

        .carousel{
            max-width:50rem;
            left:50%;
