<template>
  <section class="whole-header-container toDark">
    <header class="header-container flex relative max-width" id="nav" >
        <div class="logo toDark">
            [ALEJO<span>AVENALI</span>]
        </div>
        <nav>
            <ul class="flex">
                <li>
                    <a href="#about">About me</a>
                </li>
            </ul>
        </nav>
        <div class="moon-container absolute">
            <i class="fas fa-moon"  @click="darkMode"></i>
        </div>
        <article class="contacto">
            <a href="#contact" class="second-button">CONTACT</a>
        </article>
        <div class="burguer" @click="openMenu">
            <div class="line1"></div>
            <div class="line2"></div>
            <div class="line3"></div>
        </div>
    </header>
  </section>
</template>

<script>
export default {
    name:'Header',
    methods:{
        openMenu(){
            let line1 = document.querySelector('.line1')
            let line2 = document.querySelector('.line2')
            let line3 = document.querySelector('.line3')

            line1.style.transform = 'rotate(45deg) translate(.5rem,.35rem)'
            line2.style.transform = 'translateX(-5rem)'
            line3.style.transform = 'rotate(-45deg) translate(.5rem,-.35rem)'

            let menu = document.querySelector('.whole-menu-container')

            let linea1 = document.querySelector('.linea1')
            let linea2 = document.querySelector('.linea2')
            let linea3 = document.querySelector('.linea3')

            linea1.style.transform = 'rotate(45deg) translate(.5rem,.35rem)'
            linea2.style.transform = 'translateX(-5rem)'
            linea3.style.transform = 'rotate(-45deg) translate(.5rem,-.35rem)'

            menu.style.opacity = '1'
            menu.style.pointerEvents = 'all'

            let elements = document.querySelectorAll('.menu-container nav li')
            elements.forEach(function(element){
                element.style.opacity = '1'
                element.style.transform = 'translateX(0rem)'
            })

        },
        darkMode(){
            let value = localStorage.getItem('darkMode')
            localStorage.darkMode = !value
            let elementToDark = document.querySelectorAll('.toDark')
            elementToDark.forEach(el => el.classList.toggle("darkMode"))
            console.log(value)
        },
    },
    mounted(){
        let StorageDarkMode = localStorage.getItem('darkMode')
        let elementToDark = document.querySelectorAll('.toDark')
        if(localStorage.darkMode == "false"){
            elementToDark.forEach(el => el.classList.toggle("darkMode"))        
        }
        console.log(StorageDarkMode)
    }
}
</script>

<style lang="scss" scoped>
@import "../../variables.scss";

.whole-header-container{
    background:$background;
    a{
        color:$fonts 
    }

    .header-container{
        align-items: center;
        justify-content: space-between;
        padding:1.5rem 2rem;
        
        .logo{
            color:$cta;
            letter-spacing: .2rem;
            font-weight: 300;

            span{
                font-weight: 700;
            }
        }
        nav,
        .contacto{
            display:none
        }
        nav{
            ul{
                li{
                    margin:0 1rem
                }
            }
        }
        .moon-container{
            cursor:pointer;
            right:6rem;
            display:none;
            i{
                font-size: 1.2rem;
            }
        }
        .burguer{
            overflow: hidden;
            cursor:pointer;

            div{
                border-radius: 200px;
            }
            .line1{
                height:.2rem;
                width:1.5rem;
                background:$fonts;
                transition:.6s all
            }
            .line2{
                height:.2rem;
                width:1rem;
                background:$fonts;
                margin:.3rem 0;
                transition:.6s all
            }
            .line3{
                height:.2rem;
                width:1.5rem;
                background:$fonts;
                transition:.6s all
            }   
        }
    } 
}


@media(min-width:800px){
    .whole-header-container{
        .header-container{
            nav, .contacto{
                display:flex
            }
            .burguer{
                display:none
            }
            .moon-container{
                right:12.5rem;
            }
        }
    }
}  
@media(min-width: 1000px){
    .whole-header-container{
        .header-container{
            nav,
            .moon-container{
                right:15rem;
            }
        }
    }
}

</style>