<template>
    <section class="whole-loader-container flex">
        <section class="loader-container relative max-width flex">
            <div class="name flex">
                <span class="letter">A</span>
                <span class="letter">L</span>
                <span class="letter">E</span>
                <span class="letter">J</span>
                <span class="letter">O</span>
            </div>
            <div class="last-name flex relative">
                <span class="letter">A</span>
                <span class="letter">V</span>
                <span class="letter">E</span>
                <span class="letter">N</span>
                <span class="letter">A</span>
                <span class="letter">L</span>
                <span class="letter">I</span>
                <div class="period absolute" @animationend="fadeAway"></div>
            </div>
        </section>
    </section>
</template>

<script>
export default {
    name:'Loader',
    methods:{
        fadeAway(){
            let presentation = document.querySelector('.whole-loader-container')

            setTimeout(function(){ 
                presentation.style.opacity = '0'; 
                presentation.style.pointerEvents = 'none'
                sessionStorage.setItem('LoaderUsed', true)
            }, 10);
        },
    },
    mounted(){
        let loaderFromStorage = sessionStorage.getItem('LoaderUsed')
        let presentation = document.querySelector('.whole-loader-container')
        if(loaderFromStorage){
            presentation.style.display = 'none';
        }
    },
}
</script>

<style scoped lang="scss">
@import "../../variables.scss";

.whole-loader-container{
    min-height:100vh;
    width:100%;
    background:$background;
    position:fixed;
    z-index: $modal;
    transition:.4s all;

    .loader-container{
        font-size: 4rem;
        line-height: 1.1;
        width:fit-content;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        text-align: left;

        .name{
            color:$fonts;
            transition:.1s all;

            .letter{
                opacity:0;
            }
            .letter:nth-child(1){
                animation: appear .4s 2s forwards;
            }
            .letter:nth-child(2){
                animation: appear .4s 1s forwards;
            }
            .letter:nth-child(3){
                animation: appear .4s 1.3s forwards;
            }
            .letter:nth-child(4){
                animation: appear .4s 1.7s forwards;
            }
            .letter:nth-child(5){
                animation: appear .4s .5s forwards;
            }
        }
        .last-name {
            padding-bottom: 3rem;

            .letter{
                text-shadow: -1px -1px 0 $fonts, 1px -1px 0 $fonts, -1px 1px 0 $fonts, 1px 1px 0 $fonts;
                color:$background;
                transition:.1s all;
                opacity:0;
            }
            .period{
                background:$cta;
                border-radius: 100%;
                width:1rem; height:1rem;
                right:-2rem;
                bottom:3.8rem;
                opacity: 0;
                animation: period 1.5s 2.8s forwards;
                cursor:pointer;
                
            }
            .letter:nth-child(1){
                animation: appear .4s 1.4s forwards;
            }
            .letter:nth-child(2){
                animation: appear .4s 1.1s forwards;
            }
            .letter:nth-child(3){
                animation: appear .4s 2.4s forwards;
            }
            .letter:nth-child(4){
                animation: appear .4s .7s forwards;
            }
            .letter:nth-child(5){
                animation: appear .4s 1s forwards;
            }
            .letter:nth-child(6){
                animation: appear .4s 2s forwards;
            }
            .letter:nth-child(7){
                animation: appear .4s 1.7s forwards;
            }

        }
    }    
}
@keyframes appear{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes period {
    0%{
        opacity: 0;
        transform:scale(6)
    }
    50%{
        opacity: 1;
        transform:scale(1)
    }
    100%{
        opacity: 1;
        transform:scale(300)
    }
}


@media(min-width:700px){
    .whole-loader-container{
        .loader-container{
            font-size: 5rem;
        }
    }
}
@media(min-width:1000px){
    .whole-loader-container{
        .loader-container{
            font-size: 7rem;

            .last-name{
                .period{
                    bottom:4.2rem
                }
            }
        }
    }
    
}
</style>