<template>
    <section class="whole-contact-container toDark">
        <section class="contact-container max-width relative flex" id="contact">
            <article class="title-form flex" data-aos="fade-up" data-aos-offset="400">
                <div class="text-container">
                    <h2 v-html="title"></h2>
                    <p v-html="subtitle" class="relative"></p>
                    <img :src="arrow" alt="" class="arrow absolute">
                </div>
                <div class="icon-container flex relative" @click="clipboardMessage">
                    <div class="lines flex relative">
                        <div class="line1 absolute"></div>
                        <div class="line2 absolute"></div>
                    </div>
                    <div class="clipboard absolute">
                        <svg viewBox="0 0 100 100" class="check-line absolute toDark" xmlns="http://www.w3.org/2000/svg">
                            <path class="trazo animado" d="M16.667,62.167c3.109,5.55,7.217,10.591,10.926,15.75 c2.614,3.636,5.149,7.519,8.161,10.853c-0.046-0.051,1.959,2.414,2.692,2.343c0.895-0.088,6.958-8.511,6.014-7.3 c5.997-7.695,11.68-15.463,16.931-23.696c6.393-10.025,12.235-20.373,18.104-30.707C82.004,24.988,84.802,20.601,87,16"></path>
                        </svg>
                        <h3 v-html="clipboard"></h3>
                    </div>
                    <img :src="mail" alt="" class="mail absolute">
                    <img :src="mailbox" alt="" class="mailbox">
                </div>
            </article>
            <article class="media flex" data-aos="fade-up" data-aos-offset="10">
                <div class="left flex relative">
                    <label for="email"></label>
                    <input v-model="email" @click="clipboardMessage" class="emailDir absolute" readonly id="email"/>
                    <div class="media-icons">
                        <ul class="flex">
                            <li v-for="(icon, index) in icons" :key="index">
                                <a :href="icon.link" target="_blank" rel="noopener">
                                    <i :class="icon.class"></i>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="right">
                    <a @click="backToTop" class="back-top flex" >
                        <p v-html="backTop"></p>
                        <div class="img flex">
                            <img :src="littleArrow" alt="back-to-top" class="arrow">
                            <img :src="circle" alt="back-to-top" class="circle">
                        </div>
                    </a>
                </div>
            </article>
        </section>  
    </section>
</template>

<script>

export default {
    name:'Contact',
    data(){
        return{
            title:"Interested? <br> <span class='colouredSpan'>Let's chat!</span>",
            subtitle:`If you like my work, feel free to contact me. Let's talk about your idea. I'm here to create it with you! <br> <span class="bold">Click on the mailbox to copy my email</span>`,
            arrow:require('../../assets/resources/circle-arrow2.svg'),
            mail:require('../../assets/resources/mail.svg'),
            mailbox:require('../../assets/resources/mailbox.svg'),
            email:'alejoavenali@gmail.com',
            clipboard:'Email copied to clipboard!',
            icons:[
                {
                    class:'fab fa-linkedin-in',
                    link:'https://www.linkedin.com/in/alejo-avenali/'
                },
                {
                    class:'fab fa-git',
                    link:'https://github.com/avenali5'
                },
                {
                    class:"fab fa-behance",
                    link:'https://www.behance.net/aleavenali'
                },
                {
                    class:"fab fa-medium-m",
                    link:'https://alejoavenali.medium.com/'
                },
            ],
            backTop:'Back to top',
            littleArrow:require('../../assets/icons/little-arrow.svg'),
            circle:require('../../assets/icons/little-circle.svg'),
        }
    },
    methods:{
        clipboardMessage(){
            let modal = document.querySelector('.clipboard')
            let copyMail = document.querySelector(".emailDir");

                copyMail.setAttribute('type', 'text')
                copyMail.select()
            let copiar = document.execCommand('copy');          

            let mailIcon = document.querySelector('.icon-container .mail')
            let lines = document.querySelector('.icon-container .lines')

                mailIcon.style.top = '10rem' 
                mailIcon.style.transform = 'rotate(19deg) !important'
                lines.style.opacity = '0'

            let clipboard = document.querySelector('.clipboard')
            let path = document.querySelector(".trazo")
            let length = path.getTotalLength();

                path.style.strokeDasharray = length;
                path.style.strokeDashoffset = length;             
                
                path.classList.add("animado");
                path.style.strokeDashoffset = 0;
                clipboard.style.opacity = '1'
    

            setTimeout(function(){ 
                mailIcon.style.top = '10%' 
                lines.style.opacity = '1'
                path.classList.remove("animado");
                path.style.strokeDashoffset = length;
                clipboard.style.opacity = '0'
            }, 2000);
            

        },
        backToTop(){
            document.body.scrollTop = 0; 
            document.documentElement.scrollTop = 0;
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../variables.scss";

.whole-contact-container{
    background:$background;
    overflow: hidden;
    .contact-container{
        padding:3rem 2rem;
        flex-direction: column;

        .title-form{
            flex-direction: column;

            h2{
                line-height: 111.5%;
                font-size: 2rem;
                font-weight: 500;
            }
            p{
                font-weight: 300;
                margin:2rem 0;
                z-index: $modal;
                width:100%;
                line-height: 150%;
            }
            .arrow{
                top:13rem;
                right:2rem;
                z-index: $back;
                width:6rem;
                transform:rotate(20deg);
                opacity: .6;
            }

            .icon-container{
                flex-direction: column;
                margin:4rem 0;
                cursor:pointer;
                width:fit-content;
                left:50%;
                transform:translateX(-50%) !important;
                
                .lines{
                    margin:auto;
                    align-items: center;
                    margin-bottom: 2rem;

                    div{
                        transition:.4s all;
                        background:$fonts;
                        opacity:.1;
                        width:.3rem;
                        border-radius: 200px;
                    }
                    .line1{
                        height:3rem;
                        left:-1rem;
                        animation:fallingEffect 1s 1s infinite
                    }
                    .line2{
                        height:2rem;
                        left:-2rem;
                        animation:fallingEffect 1s .4s infinite
                    }
                }
                .clipboard{
                    text-align: center;
                    top:1rem;
                    
                    opacity:0;

                    .check-line{
                        width: 2rem;
                        fill: none;
                        stroke: $cta;
                        stroke-linejoin: round;
                        stroke-linecap: round;
                        stroke-width: 10px;
                        left: -2rem;
                    }
                    h3{
                        font-weight: 400;
                    }

                }
                .mail{
                    width:6rem;
                    left:2rem;
                    top:15%;
                    animation: floatEffect .8s .1s infinite;
                    margin-bottom:1rem;
                    transition:.9s all;
                }
                .mailbox{
                    width:10rem;
                    margin:auto;
                    margin-top: 6rem;
                }
            }
        }

        .media{
            margin-top:2rem;
            flex-direction: column;

            .left{
                align-items: center;

                a{
                    display:flex;
                    justify-content: center;
                    align-items: center;
                    i{
                        margin-right:1rem;
                        opacity:.7;
                        transition:.1s all;
                        font-size: 1.5rem;
                    }
                    i:hover{
                        opacity: 1;
                    }
                    .fa-linkedin-in:hover{
                        color:#0e76a8
                    }
                    .fa-git{
                        font-size: 1.7rem;
                    }
                    .fa-git:hover{
                        color:#f34f29
                    }
                    .fa-behance:hover{
                        color:#054eff
                    }
                    .fa-medium-m:hover{
                        color:#00AB6C
                    }
                }
                
                input{
                    opacity:0;
                    pointer-events: none;
                }
            }

            .right{
                .back-top{
                    align-items: center;
                    width:fit-content;
                    margin-top:0;
                    cursor:pointer;
                    .img{
                        transform:rotate(-90deg);
                        margin-top: 1.5rem;

                        .arrow{
                            transform:rotate(90deg);
                            margin-left:1rem;
                            position: relative;
                            right:-1rem;
                            animation:float .8s infinite;
                            transition:.7s all;
                        }
                    }
                    
                }
            }
        }
    }
}

@media(min-width:700px){
    .whole-contact-container{
        .contact-container{
            .title-form{
                h2{
                    font-size: 3rem;
                    text-align: center;
                }
                p{
                    width:50%;
                    text-align: center;
                    left:50%;
                    transform:translateX(-50%)
                }
                .arrow{
                    right:9rem;
                    transform:rotate(25deg);
                }
            }
            .media{
                flex-direction: row;
                justify-content: space-between;
            }
        }
    }
}

@media(min-width:1000px){
    .whole-contact-container{
        .contact-container{
            .title-form{
                flex-direction: row;
                justify-content: space-between;

                h2{
                    font-size: 4rem;
                    text-align: left;
                }
                p{
                    width:60%;
                    text-align: left;
                    left:0;
                    transform:translateX(0%)
                }
                .arrow{
                    left:22rem;
                    top:5rem;
                    transform:rotate(-80deg);
                }
                .icon-container{
                    margin-top:0;
                    left:-5rem;
                }
            }
        }
    }
    
}


@keyframes fallingEffect {
    0%{
        height: 3rem;
    }
    50%{
        height:2rem;
    }
    100%{
        height:3rem;
    }
}
@keyframes floatEffect {
    0%{
        transform: rotate(2deg);
    }
    50%{
        transform: rotate(-2deg);
    }
    100%{
        transform: rotate(2deg);
    }
}
</style>