import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/da-vinci',
    name: 'Project1',
    component: function () {
      return import('../views/Project1.vue')
    }
  },
  {
    path: '/form-validation',
    name: 'Project2',
    component: function () {
      return import('../views/Project2.vue')
    }
  },
  {
    path: '/url-shortener',
    name: 'Project3',
    component: function () {
      return import('../views/Project3.vue')
    }
  },
  {
    path: '/points-ecommerce',
    name: 'Project4',
    component: function () {
      return import('../views/Project4.vue')
    }
  },
]

const router = new VueRouter({
  routes,
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
})

export default router
